.faq-page {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
}

.faq-list {
  margin-bottom: 20px;
}

.faq-item {
  margin-bottom: 15px;
}

.faq-question {
  font-weight: bold;
  color: #2084e8;
  margin-bottom: 5px;
}

.faq-answer {
  margin-left: 10px;
}

.message-form {
  display: flex;
  align-items: center;
  position: sticky;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  overflow-y: auto;

  padding: 10px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

.message-input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  margin-right: 10px;
  overflow-y: auto;
}

.send-button {
  padding: 10px 15px;
  background-color: #2084e8;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.send-button:hover {
  background-color: #186bbf;
}

.sent-confirmation {
  color: green;
  font-weight: bold;
  margin-top: 10px;
  text-align: center;
}

.faqtitle {
  background-color: rgb(43, 135, 255);
  font-weight: bold;
  margin-top: 10px;
  text-align: center;
}
