.phone-frame {
  width: 375px;
  height: 667px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.header {
  background-color: #2084e8;
  color: white;
  width: 100%;
  text-align: center;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.create-button {
  display: block;
  margin: 0 auto 20px;
  padding: 10px 15px;
  background-color: #2084e8;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  text-align: center;
  width: 80%;
  font-size: 16px;
}

.listings {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 5px;
}

.listing-item {
  display: flex;
  align-items: center;
  padding-top: 2px;
  border-top: 1px solid #ddd;
  position: relative;
  height: 50px;
}

.listing-image {
  width: 50px;
  height: 100%;
  margin-right: 5px;
}

.listing-info {
  flex-grow: 1;
  text-align: left;
}

.listing-date {
  font-size: 9px;
  color: #555;
}

.listing-title {
  font-size: 16px;
  font-weight: bold;
  color: #333;
}

.sold-label {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #2084e8;
  color: white;
  font-size: 12px;
  padding: 5px 10px;
  border-bottom-left-radius: 8px;
  text-transform: uppercase;
}
