.input-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
  width: 100%;
}

.input-group label {
  font-size: 14px;
  margin-bottom: 5px;
  color: #555;
}

.input-field {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.photo-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.photo-icons {
  display: flex;
  gap: 20px;
}

.camera-icon,
.file-icon {
  font-size: 24px;
  cursor: pointer;
}

.sell-button {
  position: relative;
  /* absolute から relative に変更 */
  margin-top: auto;
  /* 上のコンテンツを押し上げてボタンを下部に配置 */
  align-self: flex-end;
  /* 右端に配置 */
  bottom: 20px;
  right: 20px;
  padding: 10px 20px;
  background-color: #2084e8;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}


.sell-button:hover {
  background-color: #186bbf;
}


.file-upload-icon {
  cursor: pointer;
  font-size: 24px;
  margin-bottom: 10px;
  /* 下に間隔を作って縦並びに */
  display: block;
  /* 縦並びにするために block に設定 */
}

.file-input {
  display: none;
  /* ファイル入力を非表示 */
}

.photo-preview {
  width: 130px;
  /* プレビュー画像を大きく */
  height: 130px;
  object-fit: cover;
  border-radius: 8px;
  margin-top: 10px;
}