.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2084e8;
  min-height: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 10px;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.edit-button {
  background: none;
  border: none;
  cursor: pointer;
}

.app-container {
  display: flex;
  justify-content: center;
  /* 横方向の中央揃え */
  align-items: flex-start;
  /* 縦方向の上詰め */
  text-align: center;
  width: 100%;
  min-height: 100vh;
  background-color: #f0f0f0;
  border: 1px solid black;
  padding: 20px;
}

.home-page {
  text-align: center;
  border: 1px solid black;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.phone-frame {
  width: 375px;
  height: 667px;
  margin: 0 auto;
  padding: 20px;
  margin-top: 100px;
  text-align: center;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow-y: auto;
}
