.profile-head {
  background-color: #2084e8;
  color: white;
  font-size: 24px;
  font-weight: bold;
  padding: 10px;
  width: 100%;
  text-align: center;
  border-radius: 8px 8px 0 0;
  margin-bottom: 20px;
  vertical-align: top;
}

.icon-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  margin-top:20px;
}

.profile-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.icon-options {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.icon-option {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

.icon-option.selected {
  border: 2px solid #007bff;
}

.profile-fields {
  width: 100%;
  text-align: left;
}

.profile-field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #aeaeae;
  width: 100%;
}

.field-label {
  font-weight: bold;
}

.field-value {
  white-space: pre-wrap;
}

.field-input {
  width: 100%;
  margin-left: 10px;
  text-align: left;
}

.edit-button {
  background: none;
  border: none;
  cursor: pointer;
}

.profile-section {
  display: flex;
  flex-direction: column;
  vertical-align: top;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  padding-bottom: 20px;
  border: 1px black;
}

.bottom-bar {
  position: absolute;
  bottom: 20px;
  height: 60px;
  width: 100%;
  padding-bottom: 30px;

  background-color: #fff;

}